import { Component } from '@angular/core';
import {FORM_DIRECTIVES, CORE_DIRECTIVES} from '@angular/common';
import {DatePicker} from 'ng2-datepicker/ng2-datepicker';

@Component({
  selector: 'reading',
  //styles: [require('./reading.style.css')],
  styles:[require('./reading.style.css')],
  template: require('./reading.template.html'),
  directives: [DatePicker, FORM_DIRECTIVES, CORE_DIRECTIVES]
})
export class Reading {
  calendar:any = new Array(35);
  month:String = "";
  monthNumber:number = new Date().getMonth();
  year:number=new Date().getFullYear();
  constructor() {
    // console.log(this.calendar);
    this.calendar =  this.getDaysArray(this.monthNumber);
    this.month = this.getMonth(this.monthNumber);
    // console.log(this.calendar);
  }
  getMonth(month){
    var monthNames = ["January", "February", "March", "April", "May", "June",
      "July", "August", "September", "October", "November", "December"
    ];
    return monthNames[month]
  }
  prevMonth(){
    if(this.monthNumber > 0){
      this.monthNumber--;
      this.month = this.getMonth(this.monthNumber);
      this.calendar =  this.getDaysArray(this.monthNumber);

    }
  }

  nextMonth(){
    if(this.monthNumber < 11){
      this.monthNumber++;
      this.month = this.getMonth(this.monthNumber);
      this.calendar =  this.getDaysArray(this.monthNumber);
    }
  }
 
  getDaysArray(month) {
    var names = [ 'sun', 'mon', 'tue', 'wed', 'thu', 'fri', 'sat' ];
    
    var currentDate = new Date();
    var date = new Date(currentDate.getFullYear(), month, 1);
    var result = [];
    var tempWeek = new Array(7)

    while (date.getMonth() == month) {
      
      if(date.getDate() == currentDate.getDate() && date.getMonth() == currentDate.getMonth()){
        tempWeek[date.getDay()] = {date:date.getDate(), current: true}
      } else{
        tempWeek[date.getDay()] = {date:date.getDate()}
      }
      if(date.getDay() == 6){
        result.push(tempWeek);
        // console.log(tempWeek);
        tempWeek = new Array(7);
      }
      // result.push(date.getDate()+"-"+names[date.getDay()]);
      date.setDate(date.getDate()+1);
    }
    
    var size = tempWeek.filter(function(value) { return value !== undefined }).length;

    if(size > 0){
      // console.log(tempWeek);
      result.push(tempWeek);
    }

    return result;

  }

  ngOnInit() {
    console.log('hello `Reading` component');
  }
}



