import { Component } from '@angular/core';
import {AppState} from './../app.service';
import { Podcasts } from './podcast.service';

@Component({
  selector: 'podcast',
   styles:[require('./podcast.style.css')],
  template: require('./podcast.template.html'),
  providers: [Podcasts]
})
export class Podcast {
  podcasts:any;

  constructor(public appState: AppState, public podService: Podcasts) {
    this.podcasts = podService.get(0, 10);
  }

  playPodcast(){

    if(!this.appState.state.visible){
       this.appState.set('visible', true);
    }
  }
  ngOnInit() {
    console.log('hello `Podcast` component');
  }
}



